<template>
  <div>
    <loading-component :value="loading"></loading-component>
    <div v-for="(post, index) in posts" :key="index">
      <post :id="post.id" :filter="filter"></post>
    </div>
  </div>
</template>

<script>
import Post from "@/components/Post"
import LoadingComponent from "./LoadingComponent";

export default {
  name: "Posts",
  components: {
    LoadingComponent,
    Post
  },
  props: ['filter', 'group_id'],
  data() {
    return {
      posts: null,
      loading: true
    }
  },
  mounted() {
    this.fetchPosts();
  },
  methods: {
    fetchPosts() {
      this.axios.get(this.api.ACTIONS.POSTS, {
        params: {
          page_size: 100,
          page: 1,
          filter: this.filter,
          group_id: this.group_id,
        }
      }).then(res => {
        this.posts = res.data.data.posts;
        this.loading = false
      })
    }
  }
}
</script>

